.bookList {
  position: fixed;
  top: 6rem;
  left: 0;
  right: 0;
  bottom: 0;
  overflow-y: scroll;
  padding: 2.5rem 6.25rem 3rem 6.25rem;
  background-color: #fafafa;
}

button {
  text-align: center;
  padding: 0.5rem 1.5rem;
  font-size: 0.813rem;
  color: white;
  background: #0290ff;
  border-radius: 3px;
  border: none;
  cursor: pointer;
}

h2 {
  font-size: 1.5rem;
  font-weight: bold;
  margin: 1.8rem 0 1.8rem 0;
}

.bookWrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: white;
  border: solid 1px #e8e8e8;
  border-radius: 4px;
  margin-bottom: 1rem;
}
